.theme-switcher {
  margin-left: 2rem;
  position: relative;
  display: flex;
  align-items: center;
}

.theme-switcher__btn {
  position: relative;
  width: 1.75rem;
  height: 1.75rem;
  color: var(--THEME-COLOR);
  transition: color 0.2s ease-in-out;
}

.theme-switcher__btn:focus {
  outline: 1px auto currentColor;
}

.theme-switcher__btn:hover {
  color: var(--THEME-COLOR-LIGHT);
}

.theme-switcher__btn svg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;  
  margin: auto;
  fill: currentColor;
}

.theme-switcher__list {
  position: absolute;
  top: 100%;
  right: 0;
  list-style: none;
  margin: 0;
  padding: 0.5rem 1rem;
  background-color: #fff;
  box-shadow: -2px 0 20px rgba(0,0,0,.07);
  transform-origin: 100% 0;
  transition: all 0.2s ease-in-out;
}

.theme-switcher__list.hidden {
  opacity: 0;
  transform: scale(0);
  z-index: -1;
}

.theme-switcher__list.active {
  opacity: 1;
  transform: scale(1);
  z-index: 1;
}

.theme-switcher__item {
  font-size: 1rem;
  margin: 0.5rem 0;
  color: var(--color-accent);
  white-space: nowrap;
}

.theme-switcher__title {
  display: block;
}

.theme-switcher__bullet {
  position: absolute;
  top: 0;
  left: 0;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 50%;
  background-color: var(--color-accent);
}

.theme-switcher__item input:checked ~ .theme-switcher__bullet {
  background-image: url("data:image/svg+xml,%3Csvg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.49373 0.758435C8.83831 0.413855 9.39698 0.413855 9.74156 0.758435C10.0861 1.10302 10.0861 1.66169 9.74156 2.00627L4.44745 7.30039C4.10287 7.64497 3.54419 7.64497 3.19961 7.30039L0.258435 4.35921C-0.0861451 4.01463 -0.0861451 3.45596 0.258435 3.11138C0.603015 2.7668 1.16169 2.7668 1.50627 3.11138L3.82353 5.42863L8.49373 0.758435Z' fill='%23FFFFFF'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center center;
}
